import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Form, Field } from 'react-final-form';

import Joke from '$components/Joke'

import styles from './index.module.scss'



const Condition = ({ when, is, children }) => (
    <Field name={when} subscription={{ value: true }}>
        {({ input: { value } }) => (value === is ? children : null)}
    </Field>
)

const Main = ({ onLike, onDislike, favouriteJokes }) => {
    const [categories, setCategories] = useState([]);
    const [jokes, setJokes] = useState([]);
    localStorage.getItem('favourites');

    const onSubmit = async (values) => {
        const option = values.option;
        const baseUrl = 'https://api.chucknorris.io/jokes'

        const url = option === 'random' ? baseUrl + '/random' : option === 'search' ? baseUrl + `/search?query=${values.search}` : baseUrl + `/random?category=${values.category}`
        const joke = await axios.get(url);
        option === 'search' ? setJokes(joke.data.result) : setJokes([{ ...joke.data }])
    }

    useEffect(() => {
        axios.get('https://api.chucknorris.io/jokes/categories').then((res) => setCategories(res.data))
    }, []);

    return (
        <div className={styles.main}>
            <div className={styles.header}>MSI 2020</div>
            <div className={styles.titleWrapper}>
                <div className={styles.title}>Hey!</div>
                <div className={styles.subtitle}>Let’s try to find a joke for you:</div>
            </div>
            <Form
                onSubmit={onSubmit}
                initialValues={{ option: 'random', category: categories[0] }}
                render={({ handleSubmit, form, submitting, pristine, values }) => (
                    <form
                        className={styles.wrapper}
                        onSubmit={handleSubmit}>
                        <div className={styles.options}>
                            <label className={styles.option}>
                                <Field
                                    name="option"
                                    component="input"
                                    type="radio"
                                    value="random"
                                />
                  Random
                </label>
                            <label className={styles.option}>
                                <Field
                                    name="option"
                                    component="input"
                                    type="radio"
                                    value="categories"
                                />
                  From categories
                </label>
                            <Condition when="option" is="categories">
                                <div className={styles.categories}>
                                    {categories.map((category, index) => (
                                        <div className={styles.category} key={index}>
                                            <Field
                                                name="category"
                                                component="input"
                                                type="radio"
                                                id={category}
                                                value={category}
                                            />
                                            <label className={styles.option} htmlFor={category}>
                                                {category}
                                            </label>
                                        </div>
                                    ))}
                                </div>
                            </Condition>
                            <label className={styles.option}>
                                <Field
                                    name="option"
                                    component="input"
                                    type="radio"
                                    value="search"
                                />
                  Search
                </label>
                            <Condition when="option" is="search">
                                <div>
                                    <Field
                                        name="search"
                                        component="input"
                                        type="text"
                                        placeholder="Free text search..."
                                        className={styles.search}
                                    />
                                </div>
                            </Condition>
                        </div>
                        <div className="buttons">
                            <div className={styles.button} onClick={handleSubmit}>Get a joke</div>
                        </div>
                    </form>
                )}
            />
            <div className={styles.jokeWrapper}>
                {jokes.length !== 0 && jokes.map(joke => {
                    const favourite = favouriteJokes && favouriteJokes.find(obj => obj.id === joke.id);
                    console.log(!!favourite);

                    return (
                        <Joke
                            key={joke.id}
                            data={joke}
                            onLike={onLike}
                            onDislike={onDislike}
                            favourite={!!favourite}
                        />
                    )
                })}
            </div>
        </div>
    );
}

export default Main;