import React, { useState } from 'react';

import moment from 'moment';

import { ReactComponent as LikeSvg } from '$svg/like.svg';
import { ReactComponent as LikedSvg } from '$svg/liked.svg';
import { ReactComponent as CommentSvg } from '$svg/comment.svg';
import { ReactComponent as LinkSvg } from '$svg/link.svg';

import styles from './index.module.scss';

const Joke = ({ data, onLike, onDislike, color = '#F8F8F8', padding = '40px', hideCategory = false, boxShadow = '', favourite = false }) => {
    const [liked, toggle] = useState(favourite ? true : false)
    const { id, value: text, updated_at: updated, url, categories } = data;

    const toggleLike = () => {
        toggle(!liked);
    }

    return (
        <div className={styles.wrapper} style={{ backgroundColor: color, padding: padding, boxShadow: boxShadow }}>
            <div className={styles.icon}>
                {favourite ? <LikedSvg onClick={() => onDislike(id, toggleLike)} /> : <LikeSvg onClick={() => onLike(data, toggleLike)} />}
            </div>
            <div className={styles.infoWrapper}>
                <div className={styles.favourite}>
                    <CommentSvg />
                </div>
                <div className={styles.info}>
                    <div className={styles.link}>
                        <span>ID: </span>
                        <a href={url} target='_blank' rel="noopener noreferrer">{id}</a>
                        <LinkSvg />
                    </div>
                    <div className={styles.text}>{text}</div>
                    <div className={styles.details}>
                        {/* <div className={styles.updated}>Last update: {moment(updated).fromNow()}</div> */}
                        <div className={styles.updated}>Last update: {Math.round(moment.duration(moment(new Date()).diff(moment(updated))).asHours())} hours ago</div>
                        {!hideCategory && categories[0] && <div className={styles.category}>{categories[0]}</div>}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Joke;